@import url(foundation.min.css);
body {
  background: url(../images/body-bg.png) repeat-y;
  overflow-x: hidden;
}

.btn {
  background-color: #2f62f2;
  border-radius: 3px;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  padding: .5rem 1rem;
  text-decoration: none;
}

.btn--submit {
  display: block;
  padding: .6rem 1rem;
  text-align: center;
}

.highlight {
  color: #2f62f2;
}

.cell.centered {
  text-align: center;
}

.cell.centered p {
  color: #767676;
  font-size: .8rem;
  font-weight: bold;
  margin-top: 0;
}

.cell--has-margin {
  margin-bottom: .4rem;
  margin-top: .4rem;
}

.grid-x.valign {
  align-items: center;
}

.has-spacer {
  margin-bottom: 3rem;
  margin-top: 3rem;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: .5em;
}

.header {
  top: -100%;
  transition: top .5s ease-in-out;
  width: 100%;
}

.header.sticky {
  position: fixed;
  top: 0;
  z-index: 99;
}

.header-wrapper {
  background-color: #215aa9;
  padding: 18px 0;
  position: relative;
}

.title-bar {
  background: transparent;
  float: right;
  height: 37px;
  padding-bottom: 0;
  padding-top: 0;
}

a.menu__link {
  color: #fff;
  font-weight: bold;
  padding: 0 1rem;
}

a.menu__link--alt {
  color: #3a3a3a;
}

a.menu__link--alt:hover {
  color: #2f62f2;
}

.list-item {
  padding: .7rem 0;
}

.list-item:last-child .menu__link {
  border: none;
}

.listing {
  list-style: none;
  margin: 0;
  padding: 0;
}

.listing__item {
  color: #414141;
  font-size: .75rem;
}

.section {
  padding: 3rem 0;
}

.section.primary {
  padding-bottom: 1.5rem;
}

.secondary {
  border-bottom: 1px solid #e4e4e4;
  border-top: 1px solid #e4e4e4;
}

.secondary h2 {
  margin-top: 0;
}

.secondary p {
  color: #777777;
  font-size: .8rem;
}

.form__text {
  border: none;
  border-radius: 3px;
  box-shadow: 1px 2px 4px inset #ccc;
  font-size: .8rem;
  padding: .8rem;
  width: 100%;
}

.tertiary h2 {
  margin-bottom: 0;
}

.footer {
  background-color: #e5eaf1;
  padding: 2rem 0 3rem;
}

.footer__menu {
  margin-bottom: 1rem;
}

.footer__menu .menu {
  justify-content: center;
}

.footer__copyright {
  color: #2c5ee6;
  text-align: center;
}

/* Small only */
@media screen and (max-width: 39.9375em) {
  .small-spacer {
    margin-bottom: 3rem;
  }
  .main-menu .menu {
    flex-direction: column;
  }
  .main-menu .menu__link {
    border: none;
  }
  .main-menu .list-item {
    border-bottom: 1px solid #09336c;
    border-top: 1px solid #1862c6;
  }
  .secondary h2 {
    text-align: center;
  }
  .secondary .max-width {
    margin-left: auto;
    margin-right: auto;
    max-width: 320px;
  }
}

/* Small and Medium */
@media screen and (max-width: 63.9375em) {
  .main-menu {
    background: #215aa9;
    border-top: 1px solid #09336c;
    position: absolute;
    right: -100%;
    top: 100%;
    transition: right .5s ease-in-out;
    width: 100%;
  }
  .main-menu.active {
    right: 0;
  }
  .small-spacer {
    margin-bottom: 3rem;
  }
  .small-spacer--minor {
    margin-bottom: 1.5rem;
  }
  .max-width h2 {
    text-align: center;
  }
}

/* Medium only */
@media screen and (min-width: 40em) and (max-width: 63.9375em) {
  .listing__item {
    float: left;
    margin-bottom: .5rem;
    text-align: left;
    width: 50%;
  }
  .listing__item:nth-child(odd) {
    clear: left;
  }
  .main-menu .menu {
    justify-content: center;
  }
}

/* Medium and up */
@media screen and (min-width: 40em) {
  a.menu__link {
    border-right: 2px solid #fff;
  }
  a.menu__link--alt {
    border-color: #2f62f2;
  }
}

/* Large and up */
@media screen and (min-width: 64em) {
  .title-bar {
    display: none;
  }
  .large-spacer {
    margin-bottom: 2rem;
    margin-top: 3rem;
  }
}

/* Large only */
@media screen and (min-width: 64em) and (max-width: 74.9375em) {
  .main-menu {
    float: right;
  }
}
